.booking-form-section {
  display: flex;
  flex-direction: column;
  padding: 100px 20px 100px 20px;
  flex: 1;
  height: 100%;
}

.booking-form-article {
  position: relative;
  width: 985px;
  margin: 15px 50px 30px 50px;
  margin: auto;
  background-color: #f2f3f4;
  padding: 20px;
  border-radius: 8px;
  border: 2.5px solid #000;
  box-shadow: 0 -1px 5px #495e57, /* Upwards shadow */ 0 5px 15px #495e57; /* Downwards shadow */
}

.form-container {
  display: grid;
  grid-gap: 29px;
  margin-left: -2px;
}

.first-row {
  display: grid;
  justify-content: center;
  grid-template-rows: 80px;
  grid-template-columns: 300px 300px 300px;
  grid-gap: 29px;
  margin-left: -8px;
}

.second-quad {
  display: grid;
  grid-template-rows: repeat(2, 90px); /* 2 rows, each 90px high */
  grid-template-columns: repeat(2, 300px); /* 2 columns, each 300px wide */
  grid-gap: 29px;
  margin-left: -8px;
}

.second-single {
  grid-column: 3 / 4; /* Span the 3rd column */
  grid-row: 1 / 3; /* Span both rows */
}

.special-request {
  display: flex;
  justify-content: center;
}

div.submit-btn {
  display: flex;
  justify-content: center;
}

.submit-btn-chakra {
  padding: 28px 96px !important;
  background-color: #3182ce !important;
  color: white !important;
  font-weight: bold !important;
  margin-top: 32px !important;
}

.booking-page-img {
  display: flex;
  justify-content: space-evenly;
  margin: 50px;
}

.booking-page-img img {
  height: 170px;
  width: 210px;
  transition: transform 0.3s ease, box-shadow 0.4s ease; /* Smooth transition */
  cursor: pointer;
}

.booking-page-img img:hover {
  transform: scale(1.3);
  box-shadow: 0 8px 16px black;
}

@media (max-width: 1150px) {
  .booking-form-article {
    max-width: 400px;
    margin: 30px auto;
    background-color: #f8f9f9;
    padding: 20px;
    border-radius: 8px;
    border: 2.5px solid #000;
    box-shadow: 0 -1px 5px #495e57, /* Upwards shadow */ 0 5px 15px #495e57; /* Downwards shadow */
  }

  .form-container {
    display: flex;
    flex-direction: column;
    flex: 1;
    gap: 1px;
  }

  .first-row {
    display: flex;
    flex-direction: column;
    flex: 1;
    gap: 1px;
  }

  .second-quad {
    display: flex;
    flex-direction: column;
    flex: 1;
    gap: -1px !important;
  }

  .second-single {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-bottom: 25px;
  }

  textarea.special-request-field {
    width: 370px;
  }

  div.submit-btn {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .booking-page-img {
    position: relative; /* Establishes a positioning context */
    width: 180px;
    height: 180px;
    transform: translate(-60px, -15px);
  }

  .booking-page-img img {
    position: absolute; /* Allows overlap */
    width: 180px;
    height: 180px;
    object-fit: cover;
    border-radius: 8px;
    border: 2px solid #e5e7e9;
    box-shadow: 0 4px 8px black;
  }

  .booking-page-img img:nth-child(1) {
    top: 0;
    left: 0;
  }

  .booking-page-img img:nth-child(2) {
    top: 70px;
    left: 95px;
  }

  .booking-page-img img:nth-child(3) {
    top: -3px;
    left: 200px;
  }

  /* On hover, bring the image to the front */
  .booking-page-img img:hover {
    z-index: 3;
  }
}

